import React, { Children, cloneElement, Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { size, first } from 'lodash/fp';
import Gnb from '~/containers/Gnb';
import Footer from '~/components/Footer';

const Background = styled.div`
  background-color: #fff;
`;

export default class HigherOrderLayout extends Component {
  static propTypes = {
    historyGoBack: PropTypes.func.isRequired,
    printPage: PropTypes.func.isRequired,
    categories: PropTypes.arrayOf(PropTypes.shape({})),
    postInformations: PropTypes.arrayOf(PropTypes.shape({
      path: PropTypes.string.isRequired,
      title: PropTypes.string.isRequired,
      tags: PropTypes.arrayOf(PropTypes.string).isRequired,
      category: PropTypes.string,
    })),
    portfolios: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    children: PropTypes.node,
    location: PropTypes.shape({
      pathname: PropTypes.string,
    }).isRequired,
  };

  static defaultProps = {
    categories: [],
    postInformations: [],
    children: null,
  };

  render() {
    const {
      historyGoBack,
      printPage,
      categories,
      postInformations,
      portfolios,
      children,
      location,
    } = this.props;

    const childrenWithProps = Children.map(
      children,
      child => cloneElement(
        child,
        {
          location,
          historyGoBack,
          printPage,
          portfolios,
        }
      )
    );

    return (
      <Background>
        <nav>
          <Gnb
            location={location}
            categories={categories}
            postInformations={postInformations}
            hasPortfolio={size(portfolios) > 0}
          />
        </nav>
        <main>
          {first(childrenWithProps)}
        </main>
        <footer>
          <Footer />
        </footer>
      </Background>
    );
  }
}
